import { io } from "socket.io-client";
import { createPeer, closePeer } from "./peer";
import { store } from "store";
import { setOnline } from "actions/globalActions";
import { onCall } from "./peer";
import { playTipAlertAudio } from "utils/utils";

const SERVER_URL = "wss://chadinuchat.hopto.org:5050";
// const SERVER_URL = "ws://fourcam.onrender.com:5050";
// const SERVER_URL = "ws://192.168.8.184:5050";

export var socket = null;

const sleep = (milliseconds) => {
    return new Promise((resolve) => setTimeout(resolve, milliseconds));
};

export const connectWithSocketServer = () => {
    console.log("start connection")
    socket = null;
    socket = io(SERVER_URL, {
        reconnect: false,
        'force new connection': true
    });

    socket.on("connect", () => {
        createPeer(socket.id);
        console.log(
            `Successfully connected to socket.io server. Connected socket.id: ${socket.id}`
        );
    });

    socket.on("status", (data) => {
        store.dispatch(setOnline(parseInt(data.online)));
    });

    socket.on("accept-new-match", (data) => {
      onCall(data.peerId)
    });
    
    socket.on("receive-tip", async (data) => {
        await receiveTip(data);
    });

    socket.on("partner-disconnected", async () => {
        // reset
        console.log("partner disconnected");
        await closePeer();
    });
}

export const emitMatched = (data) => {
    socket.emit("matched", data);
}

export const disconnectSocketServer = () => {
    try {
        socket.disconnect();
        socket = null;
    } catch {}
}

const receiveTip = async (data) => {
    await sleep(2000);
    playTipAlertAudio();
    alert(`You got a $${data?.amount} tip`);
}

export const sendTipToPartner = (data) => {
    try {
        socket.emit('send-tip', data)
    } catch {}
    playTipAlertAudio();
    alert("Your tip has been sent!");
}
