import React, { useEffect, useState } from "react";
import { useWeb3React } from "@web3-react/core";
import { Button } from "@material-ui/core";

const Microphone = ({ localStream }) => {
  const [micEnabled, setMicEnabled] = useState(true);
  var { active } = useWeb3React();

  const handleToggleMic = () => {
    if (localStream) {
      let toggledMic = !micEnabled;
      localStream.getAudioTracks().forEach((track) => {
        track.enabled = !track.enabled;
        setMicEnabled(toggledMic);
      });
    }
  };

  useEffect(() => {
    if (localStream === null) {
      setMicEnabled(true);
    }
  }, [localStream]);

  return (
    <Button
      onClick={handleToggleMic}
      variant="contained"
      style={{
        borderRadius: 50,
        marginRight: 3,
        border: "3px solid #de2500",
        backgroundColor: "transparent",
        opacity: active ? 1 : 0.5,
      }}
    >
      {!micEnabled ? (
        <img
          className="icon"
          src={require("assets/img/volume off.png").default}
        />
      ) : (
        <img
          className="icon"
          src={require("assets/img/volume up.png").default}
        />
      )}
    </Button>
  );
};

export default Microphone;
